<template>
    <div id="app">
    <h3>hello world!</h3>
    <p @click="change()">{{msg}}</p>
    <mymenu></mymenu>
    <router-link to="/home">主页</router-link>
    <router-link to="/news">新闻</router-link>
    <router-view></router-view>
    </div>
</template>

<style>
body{
  background:#ccc;

}
</style>

<script>
    import menu from './components/HelloWorld.vue'
  export default {
      data(){
        return {
          msg:'welcome to vue ^_^'
        }
      },
      methods:{
          change:function(){
              this.msg='你是我的小苹果'
          }
      },
      components:{
          'mymenu':menu
      }
  }

</script>


